	import {uniCollapse,uniCollapseItem} from "@dcloudio/uni-ui"
	export default {
		data() {
			return {
				array: ['中国', '美国', '巴西', '日本'],
                index: "",
				selectValue:"", 
				imageList:[],
				sketch:"",
				name:"",
				address:"",
				phone:"",
			}
		},
		methods: {
			bindPickerChange: function(e) {
            console.log('picker发送选择改变，携带值为', e.target.value)
            this.index = e.target.value
			},
			previewImage: function(e) {
				var current = e.target.dataset.src
				uni.previewImage({
					current: current,
					urls: this.imageList
				})
			},
			chooseImage: async function() {
					if (this.imageList.length === 9) {
						let isContinue = await this.isFullImg();
						console.log("是否继续?", isContinue);
						if (!isContinue) {
							return;
						}
					}
					uni.chooseImage({
						success: (res) => {
							this.imageList = this.imageList.concat(res.tempFilePaths);
						}
					})
				},
				isFullImg: function() {
					return new Promise((res) => {
						uni.showModal({
							content: "已经有9张图片了,是否清空现有图片？",
							success: (e) => {
								if (e.confirm) {
									this.imageList = [];
									res(true);
								} else {
									res(false)
								}
							},
							fail: () => {
								res(false)
							}
						})
					})
				},
			addApply:function(){
				var _self = this
				var myreg = /^((0\d{2,3}-\d{7,8})|(1[3456789]\d{9}))$/;
				if(_self.name == ""){
						uni.showToast({
						title:"请输入所在园区",
						icon:"none"
					})
					return false
				}else if(_self.address==""){
						uni.showToast({
						title:"请输入上门地址",
						icon:"none"
					})
					return false
				}else if(!myreg.test(_self.phone)){
						uni.showToast({
						title:"联系电话格式不正确",
						icon:"none"
					})
					return false
				}else if(_self.sketch==""){
						uni.showToast({
						title:"请输入服务内容",
						icon:"none"
					})
					return false
				}
				uni.request({
					url:_self.$api+"dockingManager/repairAdd",
					dataType:"json",
					data:{name:_self.name,address:_self.address,phone:_self.phone,sketch:_self.sketch,optionId:uni.getStorageSync("openId")},
					 header:{
						   "Content-Type": "application/text"
						 },   
					method:"GET",
					success:function(res){
						console.log(res.data)
						if(res.data ==1){ 
							uni.showToast({
								title:"申请成功", 
								success:function(){
									setTimeout(function(){
										uni.switchTab({
											url:"../../../pages/index/index"
										})
									},1000)
								}
							})
							return false 
						}else if(res.data ==99){
							uni.showModal({
								title: '提示',
								content: '此操作需用户授权，是否进行授权',
								success: function (res) {
									if (res.confirm) {
										//跳转到授权页面  
										uni.navigateTo({
											url:"../../login/login"  
										})
										console.log('用户点击确定');
									} else if (res.cancel) {
										console.log('用户点击取消');
									}
								}
							});
							return false
						}
						else if(res.data ==98){
								uni.showToast({
								title:"您尚未进行认证，请先进行认证",
								icon:"none"
							})
							return false
						}
						else{
							uni.showToast({
								title:"申请失败",
								icon:"none"
							})
							return false
						}
					}
					
				})
			
			},
		},
		 components: {uniCollapse,uniCollapseItem}
	}
